import React from "react";
import styles from "./dropdown.module.css";

const DropDown = (props) => {
  return (
    <select
      className={styles.select}
      value={props.value}
      onChange={props.onCurChange}
    >
      <option>KILO</option>
      <option>GRAM</option>
      <option>POUND</option>
    </select>
  );
};

export default DropDown;
