import styles from "./styles.module.css";
import React, { useState, useEffect } from "react";
import Input from "./components/Input";
import DropDown from "./components/DropDown";

export default function App() {
  const [firstValue, setFirstValue] = useState(0);
  const [secondValue, setSecondValue] = useState(0);
  const [dropOne, setDropOne] = useState("KILO");
  const [dropTwo, setDropTwo] = useState("GRAM");

  const CUR = {
    KILO: {
      GRAM: 1000,
      POUND: 2.2,
      KILO: 1
    },
    GRAM: {
      POUND: 0.002,
      KILO: 0.001,
      GRAM: 1
    },
    POUND: {
      KILO: 0.45,
      GRAM: 453,
      POUND: 1
    }
  };

  const dropDownOneChangeHandler = (event) => {
    setDropOne(event.target.value);
  };

  const dropDownTwoChangeHandler = (event) => {
    setDropTwo(event.target.value);
  };

  const firstValueHandler = (event) => {
    setFirstValue(event.target.value);
  };

  const secondValueHandler = (event) => {
    setSecondValue(event.target.value);
  };

  useEffect(() => {
    let x = firstValue * CUR[dropOne][dropTwo];
    setSecondValue(x);
  }, [firstValue, CUR, dropOne, dropTwo, secondValue]);

  return (
    <div className="App">
      <h1>Weight Converter</h1>
      <div className={styles.center}>
        <div class="<.picker-form>">
          <Input value={firstValue} onNewValue={firstValueHandler} />
          <DropDown
            className={styles.DropDown}
            value={dropOne}
            onCurChange={dropDownOneChangeHandler}
          />
        </div>
        <div>
          <Input value={secondValue} onNewValue={secondValueHandler} />
          <DropDown
            className={styles.DropDown}
            value={dropTwo}
            onCurChange={dropDownTwoChangeHandler}
          />
        </div>
      </div>
    </div>
  );
}
